<template>
    <div class="prize-info">
        <div class="content-top">
            <p class="text">{{$L('等待商家配货')}}</p>
            <img class="img" src="../../../assets/img/order/1.png" alt="">
        </div>
        <div class="order-message" v-if="ptype !== 'Virtual'">
            <p class="title">{{ $L('订单信息') }}</p>
            <order-detail v-bind:orderInfo="prizeInfo"></order-detail>
        </div>
        <div class="logistics-message" v-if="ptype !== 'Virtual'">
            <div class="right-content">
                <p class="title">{{ $L('物流信息') }}</p>
                <div class="deliver" v-if="deliveryList.length==0">
                    {{ $L('暂时未有物流信息') }}
                </div>
                <div v-if="deliveryList.length>0">
                    <div class="order-number">
                        {{ orderInfo.deliveryName }}：{{ orderInfo.deliveryNum }}
                    </div>
                    <logistics-info v-bind:deliveryList="deliveryList"></logistics-info>
                </div>

            </div>
        </div>
        <div class="prod-info">
            <div class="prod-item">
                <img class="left img" :src="prizeInfo.pic" alt="">
                <div class="left text">
                    <p class="chr-name">{{prizeInfo.name}}</p>
                    <p>{{prizeInfo.typeName}}</p>
                </div>
                <div class="right num">
                    <p>{{prizeInfo.fromName}}</p>
                    <p>x{{ $L('1') }}</p>
                </div>
            </div>
        </div>
        <div class="bottom-btns">
            <el-button type="primary" @click="backFunc">{{ $L('返回') }}</el-button>
        </div>
    </div>

</template>

<script>
    import OrderDetail from "../../../components/prize/orderDetail";
    import LogisticsInfo from "../../../components/prize/LogisticsInfo";
    import ProdList from "../../../components/prize/prodList";
    export default {
        name: "prizeInfo",
        components: {ProdList, LogisticsInfo, OrderDetail},
        data () {
            return {
                loading: false,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                prizeInfo: {},
                deliveryList:[],
                ptype:''
            }
        },
        created () {
           this.getData();
            if (this.$route.params.ptype !== '') {
                this.ptype = this.$route.params.ptype;
                console.log(this.ptype);
            }
        },
        methods : {
            backFunc(){
                this.$router.push({name:'prizeList',params:{type:'prod'}});
            },
            getData (){
                if(this.$route.params.id !==''){
                    const _self = this;
                    _self.loading = true;
                    let id = _self.$route.params.id;
                    _self.$api('getPrizeDetail', {id:id} , function (reData) {
                        if (reData.flag === 1) {
                            _self.prizeInfo = reData.data;
                            _self.deliveryList = reData.data.deliveryList;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'prizeList',params: {type: 'prod'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'prizeList',params: {type: 'prod'}});
                        });
                    });
                }

            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    .prize-info{
        .content-top {
            background: url('../../../assets/img/order/bgc.png') no-repeat center;
            background-size: cover;
            height: 120px;
            position: relative;
            .text{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 30px;
                font-size: 18px;
                color: #fff;
            }
            .img{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 30px;
            }
        }
        .title{
            font-weight: 700;
            margin-bottom: 10px;
            font-size: 14px;
        }
        .order-message{
            background: #fff;
            padding: 20px 10px 0;
            vertical-align: top;
            margin-bottom: 10px;
            .order-detail{
                padding-left: 0;
            }
        }
        .logistics-message{
            background: #fff;
            padding: 10px;
            vertical-align: top;
            .logistics-info{
                height: 230px;
                overflow-y: scroll;
            }
            .order-number{
                margin-bottom: 10px;
                line-height: 42px;
            }
        }
        .prod-info {
            background-color: #fff;
            margin-top: 10px;
            padding: 0 10px;
            .prod-item{
                padding: 10px 0;
                overflow: hidden;
                border-bottom: 1px solid #E1E1E1;
                .img{
                    height: 80px;
                    width: 80px;
                }
                .text{
                    display: inline-block;
                    vertical-align: top;
                    margin-left: 10px;
                    width: 40%;
                    .chr-name{
                        word-wrap:break-word;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                }
                .num{
                    text-align: right;
                }
            }
        }
        .bottom-btns{
            padding: 40px 20px;
            background: #fff;
            text-align: right;
            button{
                width: 100px;
            }
        }
    }
    .left{
        float: left;
    }
    .right{
        float: right;
    }
    .error {
        position: absolute;
        z-index: 200;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        overflow: hidden;

        .error-tips {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 320px;
            transform: translate(-50%, -50%);
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>